<template>
  <v-container>
    <v-row>
      <v-col cols="12">
        <v-combobox
          v-model="model"
          :items="items"
          item-text="description"
          :search-input.sync="search"
          hide-selected
          label="Search for symptom(s)"
          multiple
          persistent-hint
          small-chips
          solo
        >
          <template v-slot:no-data>
            <v-list-item>
              <v-list-item-content>
                <v-list-item-title>
                  No results matching "<strong>{{ search }}</strong>"
                </v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </template>
          <template v-slot:selection="{ attrs, item, parent, selected }">
            <v-chip
              v-if="item === Object(item)"
              v-bind="attrs"
              :color="`primary lighten-3`"
              :input-value="selected"
              label
              small
            >
              <span class="pr-2">
                {{ item.description }}
              </span>
              <v-icon
                small
                @click="parent.selectItem(item)"
              >fa-close</v-icon>
            </v-chip>
          </template>
        </v-combobox>
      </v-col>
      <v-col cols="12">
        <template v-if="diseases.length">
          <v-list>
            <template v-for="disease in diseases">
              <v-list-item :key="disease._id">
                <v-list-item-content>
                  <v-list-item-title>{{disease.name.toProperCase()}}</v-list-item-title>
                </v-list-item-content>
                <v-list-item-action>
                  <v-btn color="blue" @click="expand(disease)" icon>
                    <v-icon>
                      fa-expand
                    </v-icon>
                  </v-btn>
                </v-list-item-action>
              </v-list-item>
              <v-divider :key="`div-${disease._id}`" />
            </template>
          </v-list>
        </template>
        <template v-else>
          <v-card outlined class="text-center">
            <v-card-title>Search for conditions related to symptoms</v-card-title>
            <v-card-text>
              Enter the symptoms from the list of available symptoms in the search box above to find all related conditions.
              <p>Made with <v-icon color="brown">fa-coffee</v-icon> and <span style="background-color: rgb(79, 192, 141); color: white; padding: 2px 4px; border-radius: 8px 0 0 8px;">Vue</span><span style="background-color: rgb(60, 68, 92); color: white; padding: 2px 4px; border-radius: 0 8px 8px 0;">JS</span>.</p>
            </v-card-text>
          </v-card>
        </template>
      </v-col>
    </v-row>
    <v-dialog max-width="800" v-model="info.dialog">
      <template v-if="info.disease">
        <v-card>
          <v-card-title>
            {{ info.disease.name.toProperCase() }}
            <v-spacer />
            <v-btn @click="info.dialog = false" icon>
              <v-icon>
                fa-close
              </v-icon>
            </v-btn>
          </v-card-title>
          <v-card-text>
            This disease is indicated by the presence of these symptoms -
            <v-list>
              <v-list-item v-for="symptom in info.disease.symptoms" :key="symptom._id">
                <v-list-item-icon>
                  <v-icon color="primary">fa-circle</v-icon>
                </v-list-item-icon>
                <v-list-item-title>
                  {{ symptom.description }}
                </v-list-item-title>
              </v-list-item>
            </v-list>
          </v-card-text>
        </v-card>
      </template>
      <template v-else>
        <v-card>
          <v-card-text>
            No disease selected. Please select expand icon <v-icon>fa-expand</v-icon> besides the disease.
          </v-card-text>
        </v-card>
      </template>
    </v-dialog>
  </v-container>
</template>

<script>
  import axios from 'axios';

  String.prototype.toProperCase = function () {
    return this.replace(/\w\S*/g, function(txt){return txt.charAt(0).toUpperCase() + txt.substr(1).toLowerCase();});
  };

  export default {
    name: 'Checker',
    methods: {
      get_diseases(symptoms) {
        axios.post('/v1/diseases', {
          ids: symptoms.map(v => v._id)
        }).then(({ data }) => {
          this.diseases = data;
        }).catch((e) => {
          console.log(e);
        });
      },
      expand(disease) {
        this.info.disease = disease;
        this.info.dialog = true;
      }
    },
    watch: {
      model (val, old) {
        this.search = null;
        if (!val.length) {
          this.diseases = [];
          return;
        }
        this.get_diseases(val);
      }
    },
    data: () => ({
      items: [],
      diseases: [],
      model: [],
      search: null,
      info: {
        dialog: false,
        disease: null
      }
    }),
    mounted() {
      axios.get('/v1/symptoms').then(({ data }) => {
        this.items = data;
      }).catch((e) => {
        this.items = [];
      });
    }
  }
</script>
